*{
  margin: 0;
  padding: 0;
}

ul{
  padding: 0;
  margin: 0;
}

li{
  list-style: none;
}

body{
  background-color: #eee;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.hide-on-mobile{
  display: none;
}

.hide-on-desktop{
  display: block;
}

@media (min-width: 750px)
{
  .hide-on-mobile{
    display: block;
  }

  .hide-on-desktop{
    display: none;
  }

}


.side-menu{
  position: fixed;
  background-color: #000;
  width: 300px;
  height: 100%;
  box-sizing: border-box;
  padding: 30px 20px;
  transition: width .2s ease-in;
}

.side-menu.inactive {
  width: 80px;
}

.side-menu.inactive .logo{
  width: 60px;
  height: 60px;
  top: 0;
  right: 50px;
}


.side-menu .top-section {
  position: relative;
}

.side-menu .logo {
  width: 200px;
  height: 200px;
  margin: 0 40px;
  top: -60px;
  overflow: hidden;
  position: relative;
  transition: width .2s ease-in;
  
}

.side-menu .logo img {
  max-width: 100%;
  max-height: 100%;
  transition: width .2s ease-in;
}

.side-menu .top-section .toggle-menu-button {
  color: #333;
  font-size: 20px;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  cursor: pointer;
}

.side-menu.side-menu.inactive .top-section .toggle-menu-button{
  right: -50px;
}

.side-menu.side-menu.inactive .top-section .last-name {
  visibility: hidden;
  
}


.side-menu .top-section  .last-name{
  color: white;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 48%;
  right: 25%;
  font-size: xx-large;
  transform: translateY(-50%);
  opacity: 1;
  transition: opacity .2s ease-in;
}

.side-menu.side-menu.inactive .top-section .job-title {
  opacity: 0;
}

.side-menu .top-section .job-title{
  color: white;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 58%;
  right: 22%;
  font-size: large;
  transform: translateY(-40%);
  opacity: 1;
  transition: opacity .2s ease-in;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.side-menu.side-menu.inactive .top-section .school-title {
  opacity: 0;
}

.side-menu .top-section .school-title{
  color: white;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 67%;
  right: 12%;
  font-size: large;
  transform: translateY(-40%);
  opacity: 1;
  transition: opacity .2s ease-in;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.side-menu .divider {
  width: 100%;
  height: 2px;
  border-radius: 1px;
  background: #eee;
  transform: translateY(-50px);
  display: inline-flex;
}

.side-menu.side-menu.inactive .divider {
  width: 100%;
  height: 2px;
  border-radius: 1px;
  background: #eee;
  display: inline-flex;
  transform: translateY(0);
}

.side-menu .main-menu {
  transform: translateY(-20%);
}

.side-menu.inactive .main-menu {
  transform: translateY(0);
}

.side-menu .main-menu .menu-item {
  color: black;
  text-decoration: none;
  font-size: 20px;
  display: block;
  font-weight: 600;
  color: white;
  margin-top: 5px;
  cursor: pointer;
}

.side-menu .main-menu .menu-item.active {
  background-color: #666;
  border-radius: 5px;
  color: lightgray
}

.side-menu .main-menu .menu-item .menu-icon{
  display: inline-block;
  width: 40px;
  height: 40px;
  font-size: 20px;
  line-height: 40px;
  text-align: center;
}

.side-menu .main-menu .menu-item span {
  position: absolute;
  display: inline-block;
  line-height: 40px;
  opacity: 1;
  transition: opacity .2s ease-in;
}

.side-menu.inactive .main-menu .menu-item span {
  opacity: 0;
}

.side-menu .side-menu-footer{
  height: 150px;
  width: 100%;
  background-color: black;
  position: absolute;
  bottom: 0;
  left: 0;
  color: white;
}

.side-menu-footer-icons .inactive{
  margin-top: 20%;
  padding-left: 10%;
  transform: translateX(18%);
}

.side-menu-footer-icons{
  margin-top: 20%;
  padding-left: 10%;
}

.footer-icon{
  margin: 10%;
  text-decoration: none;
  cursor:pointer;
  color: white;
}

.App {
  width: 100vw;
  height: 100vh;
  background-color: #1c1c21;
  
  
}

body{
  background-color: #1c1c21;
  margin: 0;
  padding: 0;
}



.Sidebar {
  height: 100%;
  width: 250px;
  background-color: #2F4050;
}

.SidebarList {
  height: auto;
  width: 100%;
  padding: 0;

}

.SidebarList .row {
  width: 100%;
  height: 50px;
  list-style-type: none;
  margin: 0%;
  display: flex;
  flex-direction: row;
  color: white;
  justify-content: center;
  align-items: center;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;

}

.Sidebar .row:hover{
  cursor: pointer;
  background-color: #293846;
}

.SidebarList #active {
  background-color: #293846;
}

.row #icon {
  flex: 30%;
  display: grid;
  place-items: center;
}

.row #title {
  flex: 70%;
}

.container {
  margin-left: 300px;
  transition: margin-left .2s ease-in;
  padding: 50px;
  background-color: #1c1c21;
  color: #adb5bd;
  height: 100%;
  font-family: 'Lemon', serif;
}

.container.inactive{
  margin-left: 80px;
}

@media (max-width: 750px) {
  .container {
    margin-left: 0px; /* Default state on mobile */
  }

  .container.inactive {
    margin-left: 0px; /* Ensure it stays at 0px when inactive */
  }
}

.homepage{
  justify-content: center;
  margin-top: 10%;
}

.homepage h1{
  font-size: 60px;
  margin: 5px 10px;
  text-shadow: 5px 5px white;
}

.homepage .intro-header{
  padding-left: 5%;
}

.homepage h2{
  font-size: 25px;
  padding-left: 10%;
}

.homepage .second-line{
  padding-left: 15%;
}

.homepage .third-line{
  padding-left: 20%;
}


.homepage .contact-button{
  border: none;
  background-color: #adb5bd;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 25px;
  color: #666;
  transition: background-color 0.3s ease;
  font-family: 'Lemon', serif;
}

.homepage .contact-button:hover {
  background-color: darkgrey;
}

.homepage .button-div{
  margin: 10% 20%;

}

.top-bar{
  height: 45px;
  text-align: right;
  color: white;
  background-color: black;
}

.top-bar h1{
  padding-top: 5px;
  font-family: 'Lemon', serif;
