.page{
  text-align: left;
  background-color: #1c1c21;
  margin: 5% 8%;
  padding-bottom: 5%;
}

.page .cover-image-div{
  text-align: center;
  margin-bottom: 3em;
  background-color: #1c1c21;
}

.page .cover-letter-div div{
  margin-top: 1em;
  text-indent: 50px;
  background-color: #1c1c21;
}

.page .cover-letter-div{
  padding-bottom: 1em;
}

.page .cover-image-div img{
  max-width: 60%;
  height: auto;
}
.workexperience-page{
  height: 100%;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  color: white;
}

.workexperience-page .verticle-timeline-element-title{
  color: black;
}

.workexperience-page .verticle-timeline-element-subtitle{
  color: black;
}

.workexperience-page #description{
  color: black;
}

.workexperience-page h3{
  padding-top: 0.25em;
}

.vertical-timeline-element-content {
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.25),
    0 0.4em 1.25em 0 rgba(0, 0, 0, 0.15) !important;
  padding: 2em 3em !important;
}

.date {
  color: rgb(201, 251, 255);
}

#description {
  margin: 1.5em 0 2em 0;
}

.workexperience-page .button {
  text-decoration: none;
  padding: 0.5em 1em;
  border-radius: 5px;
  color: white;
}

.workButton {
  background-color: #06d6a0;
}

.workButton:hover {
  background-color: #0ac593;
}

.schoolButton {
  background-color: #f9c74f;
}

.schoolButton:hover {
  background-color: #f3bc3c;
}

@media only screen and (max-width: 1171px) {
  .vertical-timeline-element-date {
    display: block !important;
    float: none !important;
    color: rgb(44, 44, 44);
    margin-top: 1.5em;
  }
}

.about-page-header{
  padding-top: 5px;
  font-family: 'Lemon', serif;
  transform: translateY(-50%);
  align-items: center;
  text-align: center;
}

.page .carousel-container {
  width: 100%; /* Adjust based on your layout */
  height: 100px; /* Fixed height ensures uniformity */
}

.carousel-image {
  width: 100%; /* or a specific width */
  height: 600px; /* or whatever height you desire */

}

.page .overlay-text {
  position: absolute;
  bottom: 10px; /* Adjust as needed */
  left: 0;
  right: 0;
  color: white; /* Or any color you prefer */
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  margin: 0 auto;
  padding: 5px;
}

.page .carousel-title {
  text-align: center;
  margin-bottom: 15px; /* Adjust the space between the title and the carousel */
}